<template>
    <div>
        <div class="kd-page-title">
            查询结果
        </div>
        <div class="kd-page-content-wrapper wrap-info">
            <div class="item">
                <div class="lable">
                    号牌种类：
                </div>
                <div class="value">
                    {{ mortgageInfo.plateType }}
                </div>
            </div>
            <div class="item">
                <div class="lable">
                    车牌号码：
                </div>
                <div class="value">
                    {{ CONSTANT.carTypeName + mortgageInfo.plateNumber }}
                </div>
            </div>
            <div class="item">
                <div class="lable">
                    抵押状态：
                </div>
                <div class="value">
                    {{ mortgageInfo.mortgageStatus }}
                </div>
            </div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-circleleft"
                text="上一步"
                :button-handle="preStep"
            />
            <kd-button
                icon="kedaIcon-policework"
                text="业务完成"
                typeClass="complete"
            />
        </div>
    </div>
</template>

<script>
import { mapMutations, mapState } from 'vuex';

export default {
    name: 'QueryMortgageSignResult',
    computed: {
        ...mapState('mortgage', {
            mortgageInfo: 'mortgageInfo'
        })
    },
    method: {
        ...mapMutations('common', ['changeStep']),
        preStep () { // 上一步
            this.changeStep('up');
        }
    }
};
</script>
<style lang="scss" scoped>
    .wrap-info {
        font-size: 26px;
        color: #fff;
        padding: 2vw 4vw;
        position: relative;

        .item {
            line-height: 70px;
            flex-direction: row;
            font-size: 20px;
            .lable {
                display: inline-block;
                width: 200px;
                text-align: right;
            }

            .value {
                display: inline-block;
            }
        }
    }
</style>
