<template>
    <div>
        <identify-card-page v-if="currentStep === steps.findIndex(step=>step.key === 'identifyCardPage')"/>
        <face-yitu v-if="currentStep === steps.findIndex(step=>step.key === 'faceYitu')"/>
        <contrast v-if="currentStep === steps.findIndex(step=>step.key === 'contrast')"/>
        <process
            :type="1"
            v-if="currentStep === steps.findIndex(step=>step.key === 'process')"
            :handle-business="handleBusiness"
        />
		<input-car v-if="currentStep === steps.findIndex(step=>step.key === 'choiceCar')" :nextFunction="getCarAllInfo" :lastInput="lastInput"/>
        <result v-if="currentStep === steps.findIndex(step=>step.key === 'result')"/>
    </div>
</template>

<script>
import { mapMutations, mapState } from 'vuex';
import inputCar from '@/view/pageComponents/input-car';
import identifyCardPage from '@/view/public/before-work/identify-card-page';
import faceYitu from '@/view/public/before-work/face-yitu';
import contrast from '@/view/public/before-work/contrast';
import process from '@/components/process';
import result from './components/result';

import { getMortgageInfo, mortgageFaceRecognition } from '@/service/traffic/vehicle/mortgageQuery';
import { CAR_TYPE_OPTIONS, LOCATION_ID } from '@/constant/constant';

export default {
    name: 'QueryMortgageSignIndex', // 抵押标致查询
    components: {
        identifyCardPage,
        faceYitu,
        contrast,
        inputCar,
        process,
        result
    },
    data () {
        return {
            lastInput: false
        };
    },
    computed: {
        ...mapState('common', {
            steps: 'steps',
            currentStep: 'currentStep',
            identifyCard: 'identifyCard',
            binocularCamera: 'binocularCamera'
        })
    },
    methods: {
        ...mapMutations('common', ['setSteps', 'changeStep', 'changeTipPage']),
        ...mapMutations('mortgage', ['setMortgageInfo']),
        // 保存人脸对比信息
        async handleBusiness () {
            const res = await mortgageFaceRecognition({
                name: this.identifyCard.partyName,
                identifiedPhoto: this.binocularCamera
            });
            if (res && res.code === '0') {
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        preStep () { // 上一步
            this.changeStep('up');
        },
        async getCarAllInfo (params) {
            const res = await getMortgageInfo({
                plateType: params.carType,
                plateNumber: params.carLicense,
                clsbdm: params.clsbdm,
                type: 1
            });
            if (res && res.code === '0') {
                const carType = CAR_TYPE_OPTIONS.find((item, index) => {
                    if (item.value === '02') {
                        return item;
                    }
                });
                this.setMortgageInfo({
                    plateType: carType.label,
                    plateNumber: params.carLicense,
                    mortgageStatus: res.result.dybj === '0' ? '未抵押' : '已抵押'
                });
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message, imgName: 'imgCar', enableBack: true });
            }
        }
    },
    mounted () {
        if (this.CONSTANT.location === LOCATION_ID.yinchuan || this.CONSTANT.location === LOCATION_ID.zhongwei) {
            this.lastInput = true;
        }
        this.setSteps([
            { name: '身份识别', isActive: true, visible: true, key: 'identifyCardPage' },
            { name: '身份识别', isActive: false, visible: false, key: 'faceYitu' },
            { name: '身份识别', isActive: false, visible: false, key: 'contrast' },
            { name: '身份识别', isActive: false, visible: false, key: 'process' },
            { name: '输入号牌', isActive: false, visible: true, key: 'choiceCar' },
            { name: '查询结果', isActive: false, visible: true, key: 'result' }
        ]);
    }
};

</script>
